import {useState, useEffect} from 'react';
import "./TableExercise.css";

function TableHeader({headerKeys, id, sorting, HeaderClickedCallBack}) {
    return (
        <thead>
            <tr>
                {headerKeys.map((hkey, index) => 
                    <th key={id + '-h' + index} id={id + '-h' + index} onClick={() => HeaderClickedCallBack(hkey)}>
                        <div className='header-content'>
                            <div className='title-center'>
                                <div>{hkey}</div>
                            </div>
                            {hkey === sorting.key && <TableSortingIcon sortAscending={sorting.ascending} />}
                        </div>
                    </th>
                )}
            </tr>
        </thead>
    );
}

function TableSortingIcon({sortAscending}) {
    const rectangleStyle = {
        fill: 'var(--mainColor3)',
        stroke: 'var(--mainColor3)',
        strokeWidth: '5',
        opacity: '1'
    };

    const triangleStyle = {
        fill: 'var(--mainColor2)',
        opacity: '1'
    };
    
    const triangleUP = () => {
        return <path d="M20 80 L50 20 L80 80 Z" style={triangleStyle} />;
    }

    const triangleDOWN = () => {
        return <path d="M20 20 L50 80 L80 20 Z" style={triangleStyle} />;
    }

    return (
        <svg className='sort-icon' viewBox="0 0 100 100">
    	    <rect x="3" y="3" rx="20" ry="20" width="94" height="94" style={rectangleStyle} />
  		    {sortAscending ? triangleUP() : triangleDOWN()} 
	    </svg>
    );
}

function TableBody({properties, CellClickedCallBack, CancelEditCallBack, UpdateCellCallBack}) {
    const headerKeys = properties.headerKeys,
    rowData = properties.rowData,
    id = properties.id,
    editState = properties.editState;
    
    const isEditedCell = (rowIndex, headerKey) => {
        if (editState.isEdited === true &&
            editState.rowIndex === rowIndex &&
            editState.key === headerKey)
            return true;

        return false;
    };
    
    const classNameTD = (rowIndex, headerKey) => {
        //console.log([editState.row, rowIndex, editState.key, headerKey]);
        if (isEditedCell(rowIndex, headerKey))
            return 'selected';
        
        return '';
    };

    const contentTD = (value, rowIndex, headerKey) => {
        if (isEditedCell(rowIndex, headerKey))
            return <TableEdit value={value} UpdateCellCallBack={UpdateCellCallBack} CancelEditCallBack={CancelEditCallBack} />
        
        return value;
    };

    const onClickTD = (rowIndex, headerKey) => {
        // Remove onClick event from edited cell.
        if (isEditedCell (rowIndex, headerKey))
            return undefined;
        
        return () => CellClickedCallBack(rowIndex, headerKey);
    };

    return (
        <tbody>
            {rowData.map((rowItem, ri) =>
                <tr key={id + '-r' + ri}>
                    {headerKeys.map((hkey, ci) =>
                        <td key={id + '-d' + ci} className={classNameTD(ri, hkey)} onClick={onClickTD(ri, hkey)}>
                            {contentTD(rowItem[hkey], ri, hkey)}
                        </td>
                    )}
                </tr>
            )}
        </tbody>
    );
}

function Table({keys, data, id}) {
    const [headerKeys] = useState(keys);
    const [rowData, setRowData] = useState(data);
    const [sorting, setSorting] = useState({key: headerKeys[0], ascending: true});
    const [editState, setEditState] = useState ({rowIndex: 0, key: '', value: '', isEdited: false});

    useEffect(() => {
        const SortTable = (sorting) => {
            setRowData(prevRowData => {
                return [...prevRowData].sort((a, b) => {
                    // Sort upper and lower case strings properly.
                    let aValue = a[sorting.key].toString().toUpperCase(),
                        bValue = b[sorting.key].toString().toUpperCase();
                    
                    // Sort numbers properly.
                    if (Number(aValue) && Number(bValue)) {
                        aValue = Number(aValue);
                        bValue = Number(bValue);
                    }
                       
                    if (aValue === bValue)
                        return 0;
                    if (aValue > bValue)
                        if (sorting.ascending)
                            return 1;
                        else
                            return -1;
                    else
                        if (sorting.ascending)
                            return -1;
                        else
                            return 1;
                });
            });
        };
        
        SortTable(sorting);
    }, [sorting, headerKeys]);

    const HeaderClicked = (hkey) => {
        //console.log(index);
        setSorting(prevSorting => {
            return {
                ...prevSorting,
                key: hkey,
                ascending: !prevSorting.ascending
            };
        });
    };

    const CellClicked = (rowIndex, key) => {
        //console.log('cell clicked called!')
        //console.log([rowIndex, key]);
        setEditState(prevEditState => {
            return {
                ...prevEditState,
                rowIndex: rowIndex,
                key: key,
                //value: '',
                isEdited: true
            };
        });
    };

    const UpdateCell = (value) => {
        //console.log(value);
        setRowData(prevRowData => {
            const newRowData = [...prevRowData];
            newRowData[editState.rowIndex][editState.key] = value;
            return newRowData;
        });

        CancelEdit();
    }

    const CancelEdit = () => {
        //console.log('cancel edit called!')
        setEditState(prevEditState => {
            return {
                ...prevEditState,
                isEdited: false
            };
        });
    };

    return (
        <>
        <table id={id}>
            <TableHeader headerKeys={headerKeys} id={id} sorting={sorting} HeaderClickedCallBack={HeaderClicked} />
            <TableBody properties={{headerKeys: headerKeys, rowData: rowData, id: id, editState: editState}} CellClickedCallBack={CellClicked} CancelEditCallBack={CancelEdit} UpdateCellCallBack={UpdateCell} />
        </table>
        </>
    );
}

function TableEdit({value, UpdateCellCallBack, CancelEditCallBack}) {
    const [cellValue, setCellValue] = useState(value);

    useEffect(() => {
        setCellValue(value);
    }, [value]);
    
    const HandleCellEdit = (event) => {
        event.preventDefault();
        //console.log('Value Submitted: ' + cellValue);
        UpdateCellCallBack(cellValue);
    }

    return(
        <form onSubmit={HandleCellEdit}>
            <label>
                <input
                    className='cell-edit-field' 
                    type='text'
                    value={cellValue}
                    onChange={(event) => setCellValue(event.target.value)}
                />
            </label> 
            <input value='Save' type='submit' className='save-input' />
            <button className='cancel-input' onClick={() => CancelEditCallBack()}>Cancel</button>
        </form>
    );
}

function TableExercise(props) {
    const carKeys = ['model', 'year', 'color', 'registration'];
    const cars = [
        {model: 'Tesla', year: 2022, color: 'White', registration: 'ABC-123'},
        {model: 'Ferrari', year: 2022, color: 'Black', registration: 'EFG-123'},
        {model: 'Opel', year: 1999, color: 'Blue', registration: 'VVX-111'},
        {model: 'Citroen', year: 2002, color: 'White', registration: 'HHJ-223'},
        {model: 'Toyota', year: 1990, color: 'Grey', registration: 'YYJ-990'}
    ];
    
    return (
        <div id='table-exercise'>
            <Table keys={carKeys} data={cars} id='tb1' />
            <ul className='notes'>
                <li>* Click row headers to sort table.</li>
                <li>* Click cells to alter their value.</li>
            </ul>
        </div>
    )
}

export default TableExercise;