import { useState } from 'react';
import {  HashRouter as BrowserRouter, Routes, Route } from 'react-router-dom';
//import Hello from './SayHello';
//import Tweet from './Tweet'
//import GuessTheNumber from './GuessTheNumber';
import PageLayout from './UI/PageLayout';
//import PageHeader from './UI/PageHeader';
//import PageFooter from './UI/PageFooter';
import GuessTheNumberV2 from "./GuessTheNumber/GuessTheNumberV2";
import Hangman from './Hangman/Hangman';
import TableExercise from "./TableExercise/TableExercise";
import TicTacToe from './TicTacToe/TicTacToe';
import DragDrop from './DragDrop/DragDrop';
//import ComponentSelection from './UI/ComponentSelection';
//import ComponentDisplay from './UI/ComponentDisplay';
//import logo from './logo.svg';
//import Hangman from './Hangman/Hangman';
import './App.css'

function App() {
  //const [componentNameToDisplay, SetComponentNameToDisplay] = useState('guessTheNumber');
  const [theme, setTheme] = useState ('App theme-dark');

  /*
  const UserSelected = (name) => {
    //console.log(name);
    SetComponentNameToDisplay(name);
  }
  */

  const ChangeTheme = (value) => {
    if (value) {
      setTheme('App');
      return;
    }

    setTheme('App theme-dark');
  }
  
  return (
    <BrowserRouter>
      <Routes>
        <Route path='/' element={<PageLayout theme={theme} ChangeThemeCallBack={ChangeTheme} />}>
          <Route index element={<DragDrop />} />
          <Route path='TicTacToe' element={<TicTacToe />} />
          <Route path='tableExercise' element={<TableExercise rowCount={5} columnCount={4} />} />
          <Route path='hangman' element={<Hangman />} />
          <Route path='numberguess' element={<GuessTheNumberV2 min={1} max={10} />} />
          <Route path='*' element={<GuessTheNumberV2 min={1} max={10} />} />
        </Route>
      </Routes>
    </BrowserRouter>
    /*
    <div className={theme}>
      <PageHeader />
      <div className='main-content'>
        <ComponentSelection names={['guessTheNumber', 'hangman', 'TableExercise']} UserSelectedCallBack={UserSelected} />
        <ComponentDisplay name={componentNameToDisplay} />
      </div>
      <PageFooter ChangeThemeCallBack={ChangeTheme} />
    </div>
    */
  );
}


