import {useState, useEffect} from 'react';
import './ProgressIndicatorSquare';
import './Clock.css'
import ProgressIndicatorSquare from './ProgressIndicatorSquare';

export default function Clock() {
    // How often clock is refreshed in milliseconds.
    const refreshInterval = 1000;
    
    const [time, setTime] = useState(new Date());

    useEffect(() => {
        const timeCounterID = setInterval(() => {
            setTime(() => new Date());
        }, refreshInterval);

        return () => clearInterval(timeCounterID);
    }, [])
    
    // Transforms minutes and hours into 00 format.
    const formatTime = (timeNumber) => {
        if (timeNumber < 10) {
            return '0' + timeNumber;
        }
        
        return timeNumber;
    }
    
    return(
        <>
            <div className='clock'>
                <ProgressIndicatorSquare width={100} height={50} progress={time.getSeconds()/60*100}/>
                <div className='time'>
                    {formatTime(time.getHours()) + ':' + formatTime(time.getMinutes())}
                </div>
            </div>
        </>
    )
}