import './DragDrop.css';
import {useState} from 'react';
import { SortableList } from './SortableList';

export default function DragDrop(){
    const [contents, SetContents] = useState(
        [
            {id: 1, number: 1},
            {id: 2, number: 2},
            {id: 3, number: 3},
            {id: 4, number: 4},
            {id: 5, number: 5}
        ]
    );

    const Modify = (type, from, to) => {
        console.log( 'type: ' + type + ' from: ' + from + ' to: ' + to);
        let newContents = [...contents];
        const fromContent = newContents[from];
        const toContent = newContents[to];
        newContents[to] = fromContent;
        newContents[from] = toContent;

        SetContents(newContents);
    }

    return (
        <SortableList ModifyCallBack={Modify}>
        {
            contents.map((content, index) => {
               return <div key={index} className='content'>{content.number}</div>
            })
        }
        </SortableList>
    )
}