export default function ProgressIndicatorSquare({width, height, progress}) {
    const squareRadius = () => {
        return 2 * height*8/10 + 2 * width*8/10;
    }

    const squareProgress = () => {
        return squareRadius() - squareRadius() * progress/100;
    }
    
    const style = {
        fill: 'transparent',
        stroke: 'var(--mainColor2)',
        strokeWidth: '10',
        opacity: '0.5',
        strokeDasharray: squareRadius() + 'px',
        strokeDashoffset: squareProgress() + 'px'
    }
    
    return (
        <svg width={width} height={height}>
            <rect x={width/10} y={height/10} rx={height/20} ry={height/20} width={width*8/10} height={height*8/10} style={style} />
        </svg> 
    )
}