import { useRef, useEffect, useState } from 'react';

export const SortableList = ({children, ModifyCallBack}) => {
    const [fromIndex, setFromIndex] = useState(undefined);
    const [toIndex, setToIndex] = useState(undefined);
    const dragFromIndex = useRef(undefined);
    const dragToIndex = useRef(undefined);
    const [dragOverIndex, setDragOverIndex] = useState(undefined);
    
    useEffect(() => {
        if(isValidDrag()) {
            //console.log('from: ' + fromIndex + ' to: ' + toIndex);
            ModifyCallBack('move', fromIndex, toIndex);
            clear();
        }
    }, [fromIndex, toIndex, ModifyCallBack]);

    const clear = () => {
        setFromIndex(undefined);
        setToIndex(undefined);
        dragFromIndex.current = undefined;
        dragToIndex.current = undefined;
        setDragOverIndex(undefined);
    }

    const isValidDrag = () => {
        if (dragFromIndex.current !== undefined &&
            dragToIndex.current !== undefined &&
            dragFromIndex.current !== dragToIndex.current
            ) {
            return true;
        }
        return false;
    }

    const handleDragStart = (e, index) => {
        dragFromIndex.current = index;
    }

    const handleDragOver = (e, index) => {
        e.preventDefault();
        dragToIndex.current = index;
        if(isValidDrag()) {
            setDragOverIndex(index);
        }
    }

    const handleDragLeave = (e, index) => {
        setDragOverIndex(undefined);
        dragToIndex.current = undefined;
    }

    const handleDragEnd = (e, index) => {
        //console.log('dragFrom: ' + dragFromIndex.current + ' dragTo: ' + dragToIndex.current);
        if (isValidDrag()) {
            setFromIndex(dragFromIndex.current);
            setToIndex(dragToIndex.current);
        }
    }
    
    return (
        <div className='contents'>
        {
            children.map((content, index) => {
                return (
                   <div
                    key={index}
                    className={dragOverIndex === index ? 'drag-over-animation': ''}
                    draggable='true'
                    onDragStart={(e) => handleDragStart(e, index)}
                    onDragOver={(e) => handleDragOver(e, index)}
                    onDragLeave={(e) => handleDragLeave(e, index)}
                    onDragEnd={(e) => handleDragEnd(e, index)}
                   >   
                        {content}
                    </div>
                )
            })
        }    
        </div>
    )
}