import {Outlet, Link} from 'react-router-dom';
import PageHeader from './PageHeader';
import PageFooter from './PageFooter';

export default function PageLayout({theme, ChangeThemeCallBack}) {
    return (
        <>
            <div className={theme}>
                <PageHeader />
                <div className='main-content center'>
                    <div className='component-selection'>
                        <div className='container'>
                            <nav>
                                <ul>
                                    <li>
                                        <NavigationLink text='Drag and Drop' to='/' cName='navigation-link navigation-link-height' />
                                    </li>
                                    <li>
                                        <NavigationLink text='Tic Tac Toe' to='TicTacToe' cName='navigation-link navigation-link-height' />
                                    </li>
                                    <li>
                                        <NavigationLink text='Table Exercise' to='tableExercise' cName='navigation-link navigation-link-height' />   
                                    </li>
                                    <li>
                                        <NavigationLink text='Hangman Game' to='hangman' cName='navigation-link navigation-link-height' />
                                    </li>
                                    <li>
                                        <NavigationLink text='GMT Game' to='numberguess' cName='navigation-link navigation-link-height' />
                                    </li>
                                </ul>
                            </nav>
                        </div>
                    </div>
                    <div className='component-display'>
                        <div className='container'>
                            <Outlet />
                        </div>
                    </div>
                </div>
                <PageFooter ChangeThemeCallBack={ChangeThemeCallBack} />
            </div>
        </>
    )
}

function NavigationLink ({to, text, cName}) {
    return (
        <Link to={to}>
            <div className={cName}>
                <p>{text}</p>
            </div>
         </Link>
    )
}