import {useState, useEffect} from 'react';
import './TicTacToe.css';

function Board({boardState, gameState, CellClickedCallBack}) {
    const CellIcon = (value) => {
        const width = '80%', height='80%';

        switch (value) {
            case 1:
                return (
                    // Circle
                    <svg width={width} height={height}>
                        <circle cx='50%' cy='50%' r='35%' stroke='white' strokeWidth='5' fill='transparent' />  
                    </svg> 
                )
            case 2:
                return (
                    /*
                     Cross
                     Original Author: Cheris Weibo
                     License: Free for personal and commercial purpose
                    */
                    <svg width={width} height={height} viewBox='0 0 1024 1024'>
                        <path stroke='white' fill='white' d="M810.65984 170.65984q18.3296 0 30.49472 12.16512t12.16512 30.49472q0 18.00192-12.32896 30.33088l-268.67712 268.32896 268.67712 268.32896q12.32896 12.32896 12.32896 30.33088 0 18.3296-12.16512 30.49472t-30.49472 12.16512q-18.00192 0-30.33088-12.32896l-268.32896-268.67712-268.32896 268.67712q-12.32896 12.32896-30.33088 12.32896-18.3296 0-30.49472-12.16512t-12.16512-30.49472q0-18.00192 12.32896-30.33088l268.67712-268.32896-268.67712-268.32896q-12.32896-12.32896-12.32896-30.33088 0-18.3296 12.16512-30.49472t30.49472-12.16512q18.00192 0 30.33088 12.32896l268.32896 268.67712 268.32896-268.67712q12.32896-12.32896 30.33088-12.32896z"/>
                    </svg>
                )
            default:
                return(<></>)
        }
    }
    
    const CellName = (value) => {
        switch (value) {
            case 1:
                return 'cell-p1-selected'
            case 2:
                return 'cell-p2-selected'
            default:
                if (gameState === 'p1_turn')
                    return 'cell-unselected-p1-turn'
                if (gameState === 'p2_turn')
                    return 'cell-unselected-p2-turn'
        }           
    }

    return (
        <div className='board'>
            {boardState.map((row, rindex) => 
                row.map((value, cindex) =>
                    <div className={CellName(value, gameState)} key={rindex + cindex} onClick={() => CellClickedCallBack(rindex, cindex)}>{CellIcon(value)}</div>
                )
            )}                
        </div> 
    );
}

function TurnIndicator({gameState}) {
    const TurnIndicator = (gameState) => {
        switch (gameState) {
            case 'p1_turn':
                return (
                    <div className='turn-indicator turn-indicator-p1'>
                        <div>1st player's turn!</div>
                    </div> 
                );
            case 'p2_turn':
                return (
                    <div className='turn-indicator turn-indicator-p2'>
                        <div>2nd player's turn!</div>
                    </div> 
                );
            case 'p1_won':
                return (
                    <div className='turn-indicator turn-indicator-p1'>
                        <div>1st player has won!</div>
                    </div> 
                );
            case 'p2_won':
                return (
                    <div className='turn-indicator turn-indicator-p2'>
                        <div>2nd player has won!</div>
                    </div> 
                );
            case 'draw':
                return (
                    <div className='turn-indicator turn-indicator-draw'>
                        <div>Game ended in draw!</div>
                    </div>
                );
            default:
                return(<></>)

        }
    }
    
    return TurnIndicator(gameState);
}

function NewGame({gameState, ResetCallBack}) {
    const NewGameButton = (gameState, ResetCallBack) => {
        switch (gameState) {
            case 'p1_won':
            case 'p2_won':
            case 'draw':
                return (
                    <div className='button-new-game' onClick={ResetCallBack} >New Game!</div>
                )
            default:
                return (<></>)
        }
    }
    
    return NewGameButton(gameState, ResetCallBack) 
}

function TicTacToe() {
    const [boardState, setBoardState] = useState([]);
    const [gameState, setGameState] = useState();
    const [reset, setReset] = useState();
    
    useEffect(() => {
        setBoardState(() => [[0, 0, 0], [0, 0, 0], [0, 0, 0]]);
        setGameState(() => 'p1_turn');
        setReset(() => false);
    }, [reset]);

    useEffect(() => {
        const CheckForGameOver = () => {
            if (boardState.length !== 3)
                return
            
            if (CheckPlayer(1)) {
                setGameState(() => 'p1_won');
                return
            }

            if (CheckPlayer(2)) {
                setGameState(() => 'p2_won');
                return
            }

            if (CheckDraw()) {
                setGameState(() => 'draw');
                return
            }
        }

        const CheckDraw = () => {
            for (let r=0; r < boardState.length; r++) {
                for (let c=0; c < boardState[r].length; c++) {
                    if (boardState[r][c] === 0)
                    return false;
                }
            }
            
            return true;
        }

        const CheckPlayer = (player) => {
            // Check Rows
            for (let r = 0; r < boardState.length; r++)
                if (CheckRow(player, r)) {
                    return true;
                }
            // Check columns
            for (let c = 0; c < boardState[0].length; c++)
                if (CheckColumn(player, c)) {
                    return true;
                }
            // Check diagonal
            if (CheckDiagonal(player)) {
                    return true;
            }
        }

        const CheckDiagonal = (player) => {
            if (boardState[0][0] === player &&
                boardState[1][1] === player &&
                boardState[2][2] === player) {
                    return true;
            }

            if (boardState[0][2] === player &&
                boardState[1][1] === player &&
                boardState[2][0] === player) {
                    return true;
            }
        }

        const CheckColumn = (player, c) => {
            if (boardState[0][c] === player &&
                boardState[1][c] === player &&
                boardState[2][c] === player) {
                    return true;
            }
        }
    
        const CheckRow = (player, r) => {
            if (boardState[r][0] === player &&
                boardState[r][1] === player &&
                boardState[r][2] === player) {
                    return true;
            }
        }
        
        CheckForGameOver();
    }, [boardState]);
    
    const CellClicked = (r, c) => {
        if (CellAlreadyTaken(r, c))
            return; 

        switch (gameState) {
            case 'p1_turn':
                UpdateBoardState(r, c, 1);
                setGameState(() => 'p2_turn');
                break;
            case 'p2_turn':
                UpdateBoardState(r, c, 2);
                setGameState(() => 'p1_turn');
                break;
            default:
                break;
        }

        //console.log('Clicked row: ' + r + ' column: ' + c);
        //console.table(boardState);
    }

    

    const CellAlreadyTaken = (r, c) => {
        if (boardState[r][c] !== 0)
            return true;
        return false;
    }

    const UpdateBoardState= (r, c, value) => {
        setBoardState(prevBoardState =>
            prevBoardState.map((row, rindex) =>
                row.map((column, cindex) => {
                    if (rindex === r && cindex === c )
                        return value;
                    return column;
                })
            )
        );
    }

    const Reset = () => {
        setReset(() => true);
        //window.location.reload(false);
    }
    
    return (
        <div id='tic-tac-toe'>
            <TurnIndicator gameState={gameState} />
            <Board boardState={boardState} gameState={gameState} CellClickedCallBack={CellClicked} />
            <NewGame gameState={gameState} ResetCallBack={Reset} />
        </div>
    );
}

export default TicTacToe;